"use client"

import { useState } from "react"
import { Button } from "./ui/button"
import { Input } from "./ui/input"
import { Label } from "./ui/label"

export default function CheckInOut() {
  const [qrCode, setQrCode] = useState("")
  const [manualId, setManualId] = useState("")

  const handleScan = () => {
    // Placeholder for QR code scanning logic
    alert("QR code scanning not implemented yet.")
  }

  const handleManualCheckIn = () => {
    alert(`Manual check-in for ID: ${manualId}`)
    setManualId("")
  }

  const handleManualCheckOut = () => {
    alert(`Manual check-out for ID: ${manualId}`)
    setManualId("")
  }

  return (
    <div className="max-w-lg space-y-6">
      <div>
        <Label htmlFor="qrCode">Scan Visitor QR Code</Label>
        <div className="flex space-x-2">
          <Input
            id="qrCode"
            type="text"
            placeholder="Simulated QR code input"
            value={qrCode}
            onChange={e => setQrCode(e.target.value)}
          />
          <Button onClick={handleScan}>Scan</Button>
        </div>
      </div>

      <div className="border-t pt-6">
        <Label htmlFor="manualId">Manual Check-In/Out by Visitor ID</Label>
        <Input
          id="manualId"
          type="text"
          placeholder="Enter visitor ID"
          value={manualId}
          onChange={e => setManualId(e.target.value)}
        />
        <div className="flex space-x-4 mt-2">
          <Button onClick={handleManualCheckIn}>Check In</Button>
          <Button onClick={handleManualCheckOut} variant="outline">
            Check Out
          </Button>
        </div>
      </div>
    </div>
  )
}
