"use client"

import { useState } from "react"
import { Button } from "./ui/button"
import { Input } from "./ui/input"
import { Label } from "./ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "./ui/select"

export default function AppointmentScheduler() {
  const [formData, setFormData] = useState({
    visitorName: "",
    visitorEmail: "",
    date: "",
    time: "",
    host: "",
  })

  const hosts = ["Alice Johnson", "Bob Smith", "Carol Williams"] // Example hosts

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({ ...prev, [name]: value }))
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    console.log("Appointment Data:", formData)
    alert("Appointment scheduled successfully!")
    setFormData({
      visitorName: "",
      visitorEmail: "",
      date: "",
      time: "",
      host: "",
    })
  }

  return (
    <form onSubmit={handleSubmit} className="space-y-6 max-w-lg">
      <div>
        <Label htmlFor="visitorName">Visitor Name</Label>
        <Input
          id="visitorName"
          name="visitorName"
          type="text"
          placeholder="Visitor's full name"
          value={formData.visitorName}
          onChange={handleChange}
          required
        />
      </div>

      <div>
        <Label htmlFor="visitorEmail">Visitor Email</Label>
        <Input
          id="visitorEmail"
          name="visitorEmail"
          type="email"
          placeholder="Visitor's email address"
          value={formData.visitorEmail}
          onChange={handleChange}
          required
        />
      </div>

      <div>
        <Label htmlFor="date">Date</Label>
        <Input
          id="date"
          name="date"
          type="date"
          value={formData.date}
          onChange={handleChange}
          required
        />
      </div>

      <div>
        <Label htmlFor="time">Time</Label>
        <Input
          id="time"
          name="time"
          type="time"
          value={formData.time}
          onChange={handleChange}
          required
        />
      </div>

      <div>
        <Label htmlFor="host">Host</Label>
        <Select onValueChange={value => setFormData(prev => ({ ...prev, host: value }))} value={formData.host}>
          <SelectTrigger id="host" name="host" className="w-full">
            <SelectValue placeholder="Select a host" />
          </SelectTrigger>
          <SelectContent>
            {hosts.map(host => (
              <SelectItem key={host} value={host}>
                {host}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
      </div>

      <Button type="submit" className="mt-4">
        Schedule Appointment
      </Button>
    </form>
  )
}
