"use client"

import { useState } from "react"
import { Button } from "./ui/button"
import { Input } from "./ui/input"
import { Label } from "./ui/label"
import { Card } from "./ui/card"

type Host = {
  id: number
  name: string
  email: string
  phone: string
}

const initialHosts: Host[] = [
  { id: 1, name: "Alice Johnson", email: "alice@example.com", phone: "123-456-7890" },
  { id: 2, name: "Bob Smith", email: "bob@example.com", phone: "234-567-8901" },
  { id: 3, name: "Carol Williams", email: "carol@example.com", phone: "345-678-9012" },
]

export default function HostManagement() {
  const [hosts, setHosts] = useState<Host[]>(initialHosts)
  const [newHost, setNewHost] = useState({ name: "", email: "", phone: "" })

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setNewHost(prev => ({ ...prev, [name]: value }))
  }

  const addHost = () => {
    if (newHost.name && newHost.email && newHost.phone) {
      const newId = hosts.length > 0 ? hosts[hosts.length - 1].id + 1 : 1
      setHosts([...hosts, { id: newId, ...newHost }])
      setNewHost({ name: "", email: "", phone: "" })
    } else {
      alert("Please fill all fields to add a host.")
    }
  }

  return (
    <div className="max-w-3xl space-y-6">
      <div>
        <h2 className="text-2xl font-semibold mb-4">Host Directory</h2>
        <div className="space-y-4">
          {hosts.map(host => (
            <Card key={host.id} className="p-4">
              <p><strong>Name:</strong> {host.name}</p>
              <p><strong>Email:</strong> {host.email}</p>
              <p><strong>Phone:</strong> {host.phone}</p>
            </Card>
          ))}
        </div>
      </div>

      <div className="border-t pt-6">
        <h3 className="text-xl font-semibold mb-2">Add New Host</h3>
        <div className="space-y-4 max-w-md">
          <div>
            <Label htmlFor="name">Name</Label>
            <Input id="name" name="name" value={newHost.name} onChange={handleChange} />
          </div>
          <div>
            <Label htmlFor="email">Email</Label>
            <Input id="email" name="email" type="email" value={newHost.email} onChange={handleChange} />
          </div>
          <div>
            <Label htmlFor="phone">Phone</Label>
            <Input id="phone" name="phone" value={newHost.phone} onChange={handleChange} />
          </div>
          <Button onClick={addHost}>Add Host</Button>
        </div>
      </div>
    </div>
  )
}
